.note {
    color: grey;
    padding: 10px;
    font-size: 15px;
    list-style-type: none;
}

button {
    border-radius: 5px;
    padding: 8px;
    margin-left: 15px;
    border: none;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
    cursor: pointer;
}

button:hover{
    transform: translateY(-0.25em);
}